import React from 'react';

import styled from 'styled-components'
import Color from 'common/color';

export default props => {
  return (
    <>
      <ImgTitleSection>
        <div>
          <h1>버그 부화장</h1>
        </div>
      </ImgTitleSection>
      <DescriptionSection>
        그냥 개발 블로그 해볼까 하다 만들어본 <br/>
        버그 부화장 개발블로그<br/>
        버그가 나온만큼 성장할꺼에요<br/>
      </DescriptionSection>
    </>
  );
};

const ImgTitleSection = styled.div`
  background: url('https://i.pinimg.com/originals/2f/40/ec/2f40ec88436d4fe252102bcd68dec77c.png');
  background-size: cover;
  background-position: center;
  height: 200px;
  &>div {
    background: #0009;
    height: 100%;
    color: ${Color.white};
    position: relative;
    h1 {
      top: 50%;
      position: absolute;
      left: 5.5rem;
      font-size: 3.2rem;
      transform: translateY(-50%);
    }
  }
  * {margin: 0};


`;

const DescriptionSection = styled.div`
  max-width: 98rem;
  margin: 0 auto;
  padding: 2rem;
`;
